#hero {
  width: 100%;
  height: 60rem;
  display: flex;
  align-items: center;
  justify-content: center;
}
@media screen and (max-width: 480px) {
  #hero {
    max-height: 100vh;
  }
}
#hero__content {
  max-width: 120rem;
  text-align: center;
  z-index: 1;
  padding: var(--h);
}
#hero__headline {
  font-size: var(--h1);
  color: white;
  margin-bottom: 0;
}
#hero__background {
  position: absolute;
  width: 100%;
  height: 100%;
  object-fit: cover;
  top: 0;
  left: 0;
  filter: brightness(0.65);
}



