#slider {
  height: 70rem;
  max-height: 100vh;
  width: 100%;
  overflow: hidden;
}
#slider .slide {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
}
#slider .slide .headline {
  font-size: var(--h1);
  text-transform: uppercase;
  margin-bottom: 2.5rem;
  font-weight: 700;
}
#slider .background {
  position: absolute;
  width: 100%;
  height: 100%;
  object-fit: cover;
  left: 0;
  top: 0;
  filter: brightness(0.55);
}
#slider .slide {
  position: absolute;
  width: 100%;
  height: 100%;
  transition: clip-path 0.5s ease-in-out;
  background: #444;
}
#slider .slide.is-current {
  z-index: 1;
  clip-path: polygon(0 0, 100% 0, 100% 100%, 0 100%);
}
#slider .slide.is-previous,
#slider .slide .is-inactive {
  z-index: 2;
  clip-path: polygon(100% 0, 100% 0, 100% 100%, 100% 100%);
}
#slider .content {
  width: 120rem;
  max-width: 100%;
  padding: var(--h);
}
@media screen and (min-width:481px) {
  #slider .content {
    padding-right: 20rem;
  }
}
@media screen and (max-width: 480px) {
  #slider .buttons {
    display: flex;
    flex-direction: column;
    text-align: center;
  }
}
@media screen and (min-width:481px) {
  #slider .button-1 {
    margin-right: 2.5rem;
  }
}
@media screen and (max-width: 480px) {
  #slider .button-1 {
    margin-bottom: 2.5rem;
  }
}
#slider .nav {
  position: absolute;
  z-index: 3;
  max-width: 100%;
  display: flex;
}
@media screen and (min-width:481px) {
  #slider .nav {
    right: calc(50% - 60rem);
    flex-direction: column;
    padding-right: var(--h);
    height: 100%;
    justify-content: center;
  }
}
@media screen and (max-width: 480px) {
  #slider .nav {
    bottom: 0;
    justify-content: center;
    align-items: center;
    width: 100%;
    padding: 2.5rem;
  }
}
#slider .nav .item {
  display: flex;
  --size: 2.5rem;
  cursor: pointer;
}
@media screen and (min-width:481px) {
  #slider .nav .item {
    flex-direction: row-reverse;
    align-items: center;
  }
  #slider .nav .item:hover .title {
    display: block;
  }
}
#slider .nav .item.is-current .icon {
  background: white;
}
#slider .nav .title {
  display: none;
  font-size: 2.16rem;
  color: white;
  font-weight: 500;
  line-height: 100%;
}
#slider .nav .icon {
  width: 2.5rem;
  height: 2.5rem;
  border: 2px solid white;
  border-radius: 50%;
  margin: 1.5rem;
  background: transparent;
  transition: background 0.2s ease-in-out;
}
@media screen and (max-width: 480px) {
  #slider .nav .icon {
    width: 1.5rem;
    height: 1.5rem;
    margin: 1rem;
  }
}
#section-featured_tabs {
  margin-top: calc(var(--v) * -1.5);
  z-index: 1;
}
.featured_sorter {
  text-align: center;
}
.featured_sorter .nav {
  border-bottom: 2px solid rgba(0, 0, 0, 0.1);
  display: inline-block;
}
.featured_sorter .nav .item {
  display: inline-block;
  font-size: 2.16rem;
  font-weight: 500;
  padding: calc(3rem / 2);
  cursor: pointer;
}
.featured_sorter .nav .item.is-selected {
  border-bottom: 2px solid #FF2727;
}
.featured_sorter .items .item {
  display: none;
  margin-top: 5rem;
  font-size: 2.4rem;
  line-height: 1.4em;
}
.featured_sorter .items .item.is-selected {
  display: block;
}
#section-video {
  margin-top: calc(var(--v) * -1.5);
  background: #FF2727;
}
#section-video:before {
  content: '';
  position: absolute;
  top: -1px;
  left: 0;
  width: 100%;
  height: 100%;
  background: white;
  pointer-events: none;
  clip-path: polygon(0 0, 100% 0, 100% 66%, 0 33%);
}
#section-video .container-video {
  width: 100%;
  padding-bottom: 54%;
  background: #FF2727;
  border: 2.5rem solid #FF2727;
  border-bottom: none;
}
@media screen and (max-width: 480px) {
  #section-video .container-video {
    border-width: 1.5rem;
  }
}
#section-video .video {
  position: absolute;
  width: 100%;
  height: 100%;
  object-fit: cover;
  top: 0;
  left: 0;
}
.grid-menus,
.grid-community {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(350px, 1fr));
  gap: 5rem;
}
@supports not (display: grid) {
  .grid-menus,
  .grid-community {
    display: flex;
    flex-wrap: wrap;
    width: 100%;
    justify-content: center;
  }
  .grid-menus > div,
  .grid-community > div,
  .grid-menus a,
  .grid-community a {
    width: default;
    max-width: 424.24242424px;
    flex-grow: 1;
    margin: calc(var(--hpad) / 2);
  }
}
@media screen and (max-width: 480px) {
  .grid-menus,
  .grid-community {
    grid-template-columns: 1fr;
  }
}
.grid-menus .background,
.grid-community .background {
  position: absolute;
  width: 100%;
  height: 100%;
  object-fit: cover;
  top: 0;
  left: 0;
}
#section-menus {
  margin-top: calc(var(--v) * -1);
}
#section-menus .content {
  background: #FF2727;
  color: white;
  text-align: center;
  padding-bottom: var(--h);
}
#section-menus .copy {
  opacity: 0.9;
}
#section-menus .grid-menus {
  margin-top: calc(var(--v) * -2);
}
#section-menus .card {
  background: grey;
  padding-bottom: 66%;
  overflow: hidden;
}
#section-menus .card:hover {
  transform: scale(1.05);
}
#section-menus .card .background {
  position: absolute;
  width: 100%;
  height: 100%;
  object-fit: cover;
  top: 0;
  left: 0;
}
#section-menus .card .title {
  color: white;
  padding: 2.5rem;
  background: #FF2727;
  position: absolute;
  bottom: 0;
  width: 100%;
  left: 0;
  margin-bottom: 0;
}
#section-community {
  background: #f7f7f7;
  text-align: center;
}
#section-community .copy p {
  margin-bottom: 0;
}
#section-community .grid-community {
  margin-top: calc(var(--v) * -1);
}
#section-community .card-community {
  display: flex;
  flex-direction: column;
}
#section-community .card-community:hover {
  transform: scale(1.05);
}
#section-community .media {
  width: 100%;
  padding-bottom: 56.25%;
}
#section-community .category {
  position: absolute;
  top: 0;
  font-size: 1.728rem;
  transform: translateY(-50%);
  background: #FF2727;
  padding: 0.35em 0.5em 0.2em;
  line-height: 100%;
  color: white;
  font-weight: 500;
  text-transform: uppercase;
}
#section-community .content {
  padding: 2.5rem;
  background: white;
  text-align: left;
}



