#grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(350px, 1fr));
  gap: 5rem;
}
@supports not (display: grid) {
  #grid {
    display: flex;
    flex-wrap: wrap;
    width: 100%;
    justify-content: center;
  }
  #grid > div,
  #grid a {
    width: default;
    max-width: 424.24242424px;
    flex-grow: 1;
    margin: calc(var(--hpad) / 2);
  }
}
@media screen and (max-width: 480px) {
  #grid {
    grid-template-columns: 1fr;
  }
}
#grid .card-menu-marquee {
  background: grey;
  padding-bottom: 66%;
  overflow: hidden;
  cursor: pointer;
}
#grid .card-menu-marquee:hover {
  transform: scale(1.05);
}
#grid .card-menu-marquee .background {
  position: absolute;
  width: 100%;
  height: 100%;
  object-fit: cover;
  top: 0;
  left: 0;
}
#grid .card-menu-marquee .title {
  color: white;
  padding: 2.5rem;
  background: #FF2727;
  position: absolute;
  bottom: 0;
  width: 100%;
  left: 0;
  margin-bottom: 0;
}
#section-archive {
  background: #f7f7f7;
}
#section-nav {
  text-align: center;
}
#nav-food {
  margin: -0.5em -0.5em 0 0;
  font-size: 2.16rem;
}
#nav-food .category {
  border: 2px solid #444;
  padding: 1em;
  display: inline-block;
  margin: 0.5em;
  cursor: pointer;
  font-weight: 500;
}
#nav-food .category.is-selected {
  background: #FF2727;
  color: white;
}
#section-items {
  background: #f7f7f7;
}
#items {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(280px, 1fr));
  gap: 5rem;
}
@supports not (display: grid) {
  #items {
    display: flex;
    flex-wrap: wrap;
    width: 100%;
    justify-content: center;
  }
  #items > div,
  #items a {
    width: default;
    max-width: 318.18181818px;
    flex-grow: 1;
    margin: calc(var(--hpad) / 2);
  }
}
@media screen and (max-width: 480px) {
  #items {
    grid-template-columns: 1fr;
  }
}
#items .item {
  display: none;
}
#items .item.is-selected {
  display: block;
}
#items .item .description {
  font-size: 2.16rem;
}
#info {
  font-size: 2.4rem;
  margin-top: 5rem;
}



