#header {
  position: fixed;
  z-index: 1000;
  width: 100%;
  --color: white;
}
#header.is-scrolling {
  transition: all 0.2s ease-in-out;
  box-shadow: 0 5px 5px 0 rgba(0, 0, 0, 0.1);
}
#header.is-scrolling #nav {
  background: #FF2727;
}
#header.is-compact #nav {
  height: 8rem;
}
#nav {
  width: 100%;
  display: flex;
  align-items: center;
  font-size: 3rem;
  padding: 0 var(--h);
  color: white;
  background: transparent;
  justify-content: space-between;
}
#site_name {
  height: 100%;
  align-items: center;
  display: flex;
}
#siteLogo {
  height: 33%;
  max-height: 3rem;
  filter: brightness(1) invert(1);
}
#menu {
  display: flex;
  font-size: 2.16rem;
  line-height: 1.3em;
}
#menu .item-primary {
  font-weight: 500;
}
#menu .sub_menu {
  display: none;
}
#menu .item-secondary:hover {
  background: rgba(0, 0, 0, 0.1);
}
#menu .title-secondary {
  width: 100%;
  display: block;
  padding: 0.5em 1em;
}
#header.not-mobile .item-primary {
  padding: 2.5rem;
}
#header.not-mobile .sub_menu-toggle {
  display: none;
}
@media screen and (min-width:481px) {
  #nav {
    height: 13rem;
  }
}
@media screen and (max-width: 480px) {
  #nav {
    height: 9rem;
  }
}
#header .toggle {
  width: 3.75rem;
  height: 2.5rem;
  margin: 2.5rem;
  cursor: pointer;
}
#header .toggle .bar-top {
  position: absolute;
  width: 100%;
  height: 2px;
  top: 0;
  background: white;
}
#header .toggle .bar-middle {
  position: absolute;
  width: 100%;
  height: 2px;
  top: 50%;
  background: white;
}
#header .toggle .bar-bottom {
  position: absolute;
  width: 100%;
  height: 2px;
  bottom: 0;
  background: white;
}
#menu-mobile {
  position: fixed;
  width: 37.5rem;
  max-width: 100%;
  height: 100%;
  background: #FF2727;
  right: -37.5rem;
  transition: right 0.2s ease-in-out;
  z-index: 999;
  top: 0;
  display: flex;
  align-items: center;
  overflow: hidden;
}
#menu-mobile.is-open {
  right: 0;
  box-shadow:  0 -2.5rem 2.5rem 0 rgba(0, 0, 0, 0.5);
}
#menu-mobile #menu {
  flex-direction: column;
  font-size: 4rem;
  align-self: center;
  margin: auto 0;
  color: white;
  width: 37.5rem;
  height: 100%;
  justify-content: center;
  left: 0;
  transition: left 0.2s ease-in-out;
}
#menu-mobile #menu.show-sub_menu {
  left: -37.5rem;
}
#menu-mobile .item-primary {
  width: 100%;
}
#menu-mobile .item-primary .title {
  padding: 0.5em 1em;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
#menu-mobile .item-primary .title:hover {
  background: rgba(0, 0, 0, 0.1);
}
#menu-mobile .sub_menu {
  position: absolute;
  left: 100%;
  top: 0;
  width: 37.5rem;
  height: 100%;
  display: flex;
  align-items: center;
  display: none;
}
#menu-mobile .sub_menu.is-active {
  display: block;
}
#menu-mobile .sub_menu-toggle {
  width: 0.33em;
  height: 0.33em;
  border: solid white;
  border-width: 0 2px 2px 0;
  cursor: pointer;
  transform: rotate(-45deg);
  top: -0.15em;
}
#menu-mobile .item-secondary {
  font-size: 4rem;
}
#menu-mobile .exit {
  width: 3.75rem;
  height: 2.5rem;
  position: absolute;
  margin: 2.5rem;
  top: 0;
  right: 0;
  cursor: pointer;
  background: #FF2727;
}
#menu-mobile .exit:before {
  content: '';
  position: absolute;
  width: 100%;
  height: 2px;
  top: 50%;
  left: 50%;
  background: white;
  transform: translate(-50%, -50%) rotate(45deg);
}
#menu-mobile .exit:after {
  content: '';
  position: absolute;
  width: 100%;
  height: 2px;
  top: 50%;
  left: 50%;
  background: white;
  transform: translate(-50%, -50%) rotate(-45deg);
}
#menu-mobile.sub_menu-is-open .exit:before {
  left: 0;
  top: 50%;
  width: 100%;
  height: 2px;
  transform: translateY(-50%);
}
#menu-mobile.sub_menu-is-open .exit:after {
  left: 0;
  top: 50%;
  width: 1.5rem;
  height: 1.5rem;
  border: solid white;
  border-width: 0 0 2px 2px;
  transform: translateY(-50%) rotate(45deg);
  background: none;
}
#header.not-mobile .item-primary:hover .sub_menu {
  display: block;
}
#header.not-mobile .item-primary.has-children > .title:after {
  content: '';
  width: 5px;
  height: 5px;
  background: var(--color);
  margin-left: 0.25em;
  display: inline-block;
  border-radius: 50%;
  vertical-align: middle;
}
#header.not-mobile .sub_menu {
  position: absolute;
  top: 100%;
  left: 50%;
  transform: translateX(-50%);
  background: #FF2727;
  color: white;
  width: 25rem;
  box-shadow:  0 0 2.5rem 0 rgba(0, 0, 0, 0.2);
}
#header.not-mobile .item-secondary {
  font-size: 1.728rem;
}



